'use client';

import React from 'react';
import Image from 'next/image';
import { Section, Card, Tag } from '../ui';
import { selectedWork } from '../../config/content';

// Project Card component for case study format
const ProjectCard = ({ problem, built, outcome, stack, links, image }) => {
  return (
    <Card hover className="flex flex-col h-full group">
      {/* Project Image */}
      {image && (
        <div className="rounded-xl overflow-hidden border border-dark-border mb-6 relative h-52 bg-dark-elevated">
          <Image 
            src={image} 
            alt="Project" 
            fill 
            className="object-cover transition-transform duration-500 group-hover:scale-105" 
          />
          <div className="absolute inset-0 bg-gradient-to-t from-dark-surface/60 to-transparent" />
        </div>
      )}

      {/* Problem */}
      <div className="mb-5">
        <h4 className="text-text-tertiary text-xs uppercase tracking-[0.15em] mb-2 font-medium">Problem</h4>
        <p className="text-text-secondary text-sm leading-relaxed">{problem}</p>
      </div>

      {/* What I Built */}
      <div className="mb-5">
        <h4 className="text-text-tertiary text-xs uppercase tracking-[0.15em] mb-2 font-medium">Built</h4>
        <p className="text-text-secondary text-sm leading-relaxed">{built}</p>
      </div>

      {/* Outcome */}
      <div className="mb-6">
        <h4 className="text-text-tertiary text-xs uppercase tracking-[0.15em] mb-2 font-medium">Outcome</h4>
        <p className="text-text-primary font-semibold">{outcome}</p>
      </div>

      {/* Stack */}
      <div className="mb-6">
        <div className="flex flex-wrap gap-2">
          {stack.map((tech, i) => <Tag key={i}>{tech}</Tag>)}
        </div>
      </div>

      {/* Links */}
      {links && Object.keys(links).length > 0 && (
        <div className="flex gap-6 mt-auto pt-4 border-t border-dark-border">
          {links.appStore && (
            <a 
              href={links.appStore} 
              target="_blank" 
              rel="noopener noreferrer" 
              className="text-text-secondary hover:text-accent transition-colors duration-200 text-sm font-medium inline-flex items-center gap-1"
            >
              View App
              <span className="transition-transform duration-200 group-hover:translate-x-0.5">→</span>
            </a>
          )}
          {links.website && (
            <a 
              href={links.website} 
              target="_blank" 
              rel="noopener noreferrer" 
              className="text-text-secondary hover:text-accent transition-colors duration-200 text-sm font-medium inline-flex items-center gap-1"
            >
              Website
              <span className="transition-transform duration-200 group-hover:translate-x-0.5">→</span>
            </a>
          )}
        </div>
      )}
    </Card>
  );
};

const Work = () => {
  const featuredProjects = selectedWork.filter(p => p.featured);

  return (
    <Section id="work">
      <div className="mb-14">
        <h2 className="text-h1 font-bold text-text-primary mb-3">Selected Work</h2>
        <p className="text-text-secondary text-lg">Problems I've solved with product engineering</p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        {featuredProjects.map(project => (
          <ProjectCard key={project.id} {...project} />
        ))}
      </div>
    </Section>
  );
};

export default Work;
